<template>
    <the-header title='RY' @func="getMsg"></the-header>

    <keep-alive>
    <component :is="selectResource"></component>
    </keep-alive>
</template>

<script>
import TheHeader from './TheHeader.vue'
import StoredResource from './StoredResource.vue'
import AddResource from './AddResource.vue'
export default {
    components:{
        TheHeader,
        StoredResource,
        AddResource
    },
    data(){
        return{
            selectResource:'stored-resource',
            storedResources:[
                {id:'google',
                title:'google',
                description:'This is google',
                link:'https://google.org'},
                {id:'google',
                title:'google',
                description:'This is google',
                link:'https://google.org'},

            ]
        }
    },
    methods:{
         getMsg(data){
        this.selectResource = data
        },
        setSelectResource(tab){
            this.selectResource = tab;
        },
        addResource(title,description,url){
            const newResource = {
                id:new Date().toISOString(),
                title:title,
                description:description,
                link:url
            }
            this.storedResources.unshift(newResource);
            this.selectResource='stored-resource';
        },
        removeResource(resId){
            const resIndex = this.storedResources.findIndex(res => res.id === resId);
            this.storedResources.splice(resIndex,1);

        }
    },
    computed:{
        storedResourceStyle(){
            return this.selectResource === 'stored-resource' ? 'flat':null
        },
        AddResourceStyle(){
            return this.selectResource === 'add-resource' ? 'flat':null
        }
    },
    provide(){
        return{
            resources:this.storedResources,
            addResource:this.addResource,
            removeResource:this.removeResource,
        }
    }
}
</script>