<template>
    <header>
        <h1>{{title}}</h1>
        <base-button @click="setSelectResource('add-resource')"
        :mode="storedResourceStyle" class="headButton">Add resource</base-button>
        <base-button @click="setSelectResource('stored-resource')"
        :mode="AddResourceStyle" class="headButton">Stored resource</base-button>
    </header>
</template>

<script>
export default {
    props:['title'],
    data(){
        return{
            selectResource:'stored-resource',
        }
    },
    methods:{
      setSelectResource(tab){
            this.selectResource = tab;
            this.$emit('func',this.selectResource)
        },
    },
     computed:{
        storedResourceStyle(){
            return this.selectResource === 'stored-resource' ? 'flat':null
        },
        AddResourceStyle(){
            return this.selectResource === 'add-resource' ? 'flat':null
        }
    },
}
</script>

<style scoped>
header {
  width: 100%;
  height: 5rem;
  background-color: #5d6f9b;

  align-items: center;
}

header h1 {
  color: white;
  float:left;
  margin: 20px; 

}

.headButton{
  margin: 20px; 
  float:right;
}


</style>