<template>
    <base-dialog v-if="inputIsInvalid" title="Invalid Input" @close="confirmError">
      <template #default>
        <p>Pls enter at least a word</p>
      </template>
      <template #actions>
        <base-button @click="confirmError">OK</base-button>
      </template>
    </base-dialog>
    <base-card>
      <form @submit.prevent="passNewResource">
          <div class="form-control">
              <label for="title">title</label>
              <input id="title" name="title" type="text" ref="inputTitle"/>
          </div>
           <div class="form-control">
              <label for="description">description</label>
              <textarea id="description" name="description" rows="3" ref="inputDescription"></textarea>
          </div>
          <div class="form-control">
              <label for="link">link</label>
              <input id="link" name="link" type="url" ref="inputLink"/>
          </div>
          <div>
              <base-button type="submit">Add resource</base-button>
          </div>
      </form>
    </base-card>
</template>

<script>
import BaseButton from '../UI/BaseButton.vue';
export default {
    inject:['addResource'],
    props:{
      BaseButton   
    },   
    data(){
        return{
          inputIsInvalid: false,

        };
    },
    methods:{
        passNewResource(){
            const newTitle = this.$refs.inputTitle.value;
            const newDescription = this.$refs.inputDescription.value;
            const newLink = this.$refs.inputLink.value;

            if(newTitle.trim() === '' || newDescription.trim() ==='' || newLink.trim() ==='' ){
              this.inputIsInvalid = true;
              return;
            }
            this.addResource(newTitle,newDescription,newLink);
        },

        
        confirmError(){
          this.inputIsInvalid = false;
        }
    }
    
}
</script>

<style scoped>
label {
  font-weight: bold;
  display: block;
  margin-bottom: 0.5rem;
}

input,
textarea {
  display: block;
  width: 100%;
  font: inherit;
  padding: 0.15rem;
  border: 1px solid #ccc;
}

input:focus,
textarea:focus {
  outline: none;
  border-color: #3a0061;
  background-color: #f7ebff;
}

.form-control {
  margin: 1rem 0;
}
</style>